import Laraplay from './theme/index';

require('./bootstrap');

const WOW = require('wow.js');

require('./libs/index.min');

window.Laraplay = Laraplay;

const customAttributes = () => {
    $('[data-bg-color]').each((a, b) => {
        $(b).css('cssText', `background: ${$(b).data('bg-color')} !important;`);
    });
    $('[data-bg-img]').each((a, b) => {
        $(b).css('background-image', `url(${$(b).data('bg-img')})`);
    });
    $('[data-text-color]').each((a, b) => {
        $(b).css('color', $(b).data('text-color'));
    });
    $('[data-font-size]').each((a, b) => {
        $(b).css('font-size', $(b).data('font-size'));
    });
    $('[data-height]').each((a, b) => {
        $(b).css('height', $(b).data('height'));
    });
    $('[data-width]').each((a, b) => {
        $(b).css('width', $(b).data('width'));
    });
    $('[data-border]').each((a, b) => {
        $(b).css('border', $(b).data('border'));
    });
    $('[data-margin-top]').each((a, b) => {
        $(b).css('margin-top', $(b).data('margin-top'));
    });
    $('[data-margin-right]').each((a, b) => {
        $(b).css('margin-right', $(b).data('margin-right'));
    });
    $('[data-margin-bottom]').each((a, b) => {
        $(b).css('margin-bottom', $(b).data('margin-bottom'));
    });
    $('[data-margin-left]').each((a, b) => {
        $(b).css('margin-left', $(b).data('margin-left'));
    });
};

const scrollTop = () => {
    if ($(window).scrollTop() > 600) {
        $('.scrollToTop').fadeIn();
    } else {
        $('.scrollToTop').fadeOut();
    }
};

const scroolToTopOnClick = () => {
    $(document).on('click', '.scrollToTop', () => {
        $('html, body').animate(
            {
                scrollTop: 0,
            },
            800,
        );

        return false;
    });
};

(($) => {
    $(document).ready(() => {
        // init
        customAttributes();
        scroolToTopOnClick();
        // Footer
        const yearElement = document.querySelector('.year');

        if (typeof yearElement === 'object') {
            yearElement.innerHTML =
                `Copyright &copy; ${new Date().getFullYear()}  <a href="https://laraplay.com/">Laraplay</a>.` +
                'All Rights Reserved';
        }

        // Owl Slider
        const $owlCarouselCol = $('.fullwidth-carousel');

        if ($owlCarouselCol.length > 0) {
            $owlCarouselCol.each((a, b) => {
                const dataDuration =
                    $(b).data('duration') === undefined ? 4000 : $(b).data('duration');

                $(b).owlCarousel({
                    rtl: false,
                    autoplay: true,
                    autoplayTimeout: dataDuration,
                    loop: true,
                    items: 1,
                    navElement: 'div',
                    nav: true,
                    dots: false,
                    navText: [
                        '<i class="fa fa-arrow-left"></i>',
                        '<i class="fa fa-arrow-right"></i>',
                    ],
                });
            });
        }

        const owlCarousel2col = $('.owl-carousel-2col');

        if (owlCarousel2col.length > 0) {
            owlCarousel2col.each((a, b) => {
                const dataDots = $(b).data('dots') === undefined ? false : $(b).data('dots');
                const dataNav = $(b).data('nav') === undefined ? false : $(b).data('nav');
                const dataDuration =
                    $(b).data('duration') === undefined ? 4000 : $(b).data('duration');

                $(b).owlCarousel({
                    rtl: false,
                    autoplay: true,
                    autoplayTimeout: dataDuration,
                    loop: true,
                    items: 2,
                    margin: 15,
                    dots: dataDots,
                    nav: dataNav,
                    navText: [
                        '<i class="pe-7s-angle-left"></i>',
                        '<i class="pe-7s-angle-right"></i>',
                    ],
                    responsive: {
                        0: {
                            items: 1,
                            center: false,
                        },
                        480: {
                            items: 1,
                            center: false,
                        },
                        600: {
                            items: 1,
                            center: false,
                        },
                        750: {
                            items: 2,
                            center: false,
                        },
                        960: {
                            items: 2,
                        },
                        1170: {
                            items: 2,
                        },
                        1300: {
                            items: 2,
                        },
                    },
                });
            });
        }

        const owlCarousel4col = $('.owl-carousel-4col');

        if (owlCarousel4col.length > 0) {
            owlCarousel4col.each((a, b) => {
                const dataDots = $(b).data('dots') === undefined ? false : $(b).data('dots');
                const dataNav = $(b).data('nav') === undefined ? false : $(b).data('nav');
                const dataDuration =
                    $(b).data('duration') === undefined ? 4000 : $(b).data('duration');

                $(this).owlCarousel({
                    rtl: false,
                    autoplay: true,
                    autoplayTimeout: dataDuration,
                    loop: true,
                    items: 4,
                    margin: 15,
                    dots: dataDots,
                    nav: dataNav,
                    navText: [
                        '<i class="pe-7s-angle-left"></i>',
                        '<i class="pe-7s-angle-right"></i>',
                    ],
                    responsive: {
                        0: {
                            items: 1,
                            center: true,
                        },
                        480: {
                            items: 1,
                            center: false,
                        },
                        600: {
                            items: 3,
                            center: false,
                        },
                        750: {
                            items: 3,
                            center: false,
                        },
                        960: {
                            items: 3,
                        },
                        1170: {
                            items: 4,
                        },
                        1300: {
                            items: 4,
                        },
                    },
                });
            });
        }

        // parallax
        if ($(window).width() >= 1200) {
            const scrolled = $(window).scrollTop();

            $('.content-fade-effect .home-content .home-text')
                .css('padding-top', `${scrolled * 0.061}%`)
                .css('opacity', 1 - scrolled * 0.0012);
        }

        // funfact

        const animateNumber = $('.animate-number');

        if (animateNumber.length > 0) {
            animateNumber.appear();
            $(document.body).on('appear', '.animate-number', () => {
                animateNumber.each((a, b) => {
                    const currentItem = $(b);

                    if (!currentItem.hasClass('appeared')) {
                        currentItem
                            .animateNumbers(
                                currentItem.attr('data-value'),
                                true,
                                parseInt(currentItem.attr('data-animation-duration'), 10),
                            )
                            .addClass('appeared');
                    }
                });
            });
        }

        if (!Laraplay.browser().isMobile() && $(window).width() >= 800) {
            $('.parallax').each((a, b) => {
                const dataParallaxRatio =
                    $(b).data('parallax-ratio') === undefined ? '0.5' : $(b).data('parallax-ratio');

                $(b).parallax('50%', dataParallaxRatio);
            });
        } else {
            $('.parallax').addClass('mobile-parallax');
        }

        // Custom Accordion
        $('.r-accordion-heading').on('click', (e) => {
            const $t = e.target;
            const accordionBody = $($t).next('.r-accordion-body');

            $($t).parents('div.col-lg-6.col-md-12').find('.r-accordion-body').slideUp();
            $($t).parents('div.col-lg-6.col-md-12').find('i').removeClass('fa-arrow-circle-up');
            $($t).parents('div.col-lg-6.col-md-12').find('i').addClass('fa-arrow-circle-down');

            if (accordionBody.is(':hidden')) {
                accordionBody.slideDown();
                $($t).find('i').toggleClass('fa-arrow-circle-up');
                $($t).find('i').toggleClass('fa-arrow-circle-down');
            } else {
                accordionBody.slideUp();
                $($t).find('i').toggleClass('fa-arrow-circle-up');
                $($t).find('i').toggleClass('fa-arrow-circle-down');
            }
        });

        $(document).on('scroll', () => {
            scrollTop();
        });
        const wow = new WOW({
            mobile: false, // trigger animations on mobile devices (default is true)
        });

        wow.init();
    });
})(jQuery);
